$susy: (
  flow: ltr,
  math: fluid,
  output: float,
  gutter-position: after,
  container: 1020px,
  container-position: center,
  columns: 4,
  gutters: .25,
  column-width: false,
  global-box-sizing: content-box,
  last-flow: to,
  debug: (
          image: show,
          color: rgba(#66f, .25),
          output: background,
          toggle: top right,
  ),
  use-custom: (
          background-image: true,
          background-options: false,
          box-sizing: true,
          clearfix: false,
          rem: true,
  )
);

.container {
  @include container;
}

.content {
  @include span(8 of 12);
}

aside {
  @include span(last 4 of 12);
}